<template>
	<v-app id="inspire">
		<v-main>
			<v-container class="fill-height" fluid>
				<v-row align="center" justify="center">
					<v-col cols="12" sm="8" md="4">
						<v-card class="elevation-12">
							<v-toolbar color="primary" dark flat>
								<v-toolbar-title>Login Employee Management</v-toolbar-title>
								<v-spacer></v-spacer>
								<v-tooltip bottom>
									<!-- <template v-slot:activator="{ on }">
                    <v-btn
                      :href="source"
                      icon
                      large
                      target="_blank"
                      v-on="on"
                    >
                      <v-icon>mdi-code-tags</v-icon>
                    </v-btn>
                  </template> -->
									<span>Source</span>
								</v-tooltip>
							</v-toolbar>
							<v-card-text>
								<v-form>
									<v-text-field
										label="Login"
										name="login"
										prepend-icon="mdi-account"
										type="text"
										v-model="username"
									></v-text-field>

									<v-text-field
										id="password"
										label="Password"
										name="password"
										prepend-icon="mdi-lock"
										type="password"
										v-model="password"
									></v-text-field>
								</v-form>
							</v-card-text>
							<v-card-actions>
								<v-spacer></v-spacer>
								<v-btn color="primary" @click="handleSubmit">Login</v-btn>
							</v-card-actions>
						</v-card>
					</v-col>
				</v-row>
			</v-container>
		</v-main>
	</v-app>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
	data() {
		return {
			username: "",
			password: "",
			submitted: false,
		};
	},
	computed: {
		...mapState("account", ["status"]),
	},
	created() {
		this.logout();
	},
	methods: {
		...mapActions("account", ["login", "logout"]),
		handleSubmit() {
			//console.log(this.username);
			this.submitted = true;
			const { username, password } = this;
			if (username && password) {
				this.login({ username, password });
			}
		},
	},
};
</script>
